import {
  Box,
  Paper,
  TextField as MuiTextField,
  TextFieldProps,
  Typography,
  Button,
  useMediaQuery,
  FormControl,
  InputLabel,
  ListSubheader,
  Select,
  MenuItem,
  TextareaAutosize
} from '@mui/material'
import { Helmet } from 'react-helmet-async'

import { styled, useTheme } from '@mui/material/styles'
import axios, { AxiosResponse } from 'axios'
import { useNavigate, useParams } from 'react-router'
import { useEffect, useState } from 'react'
import departments from 'src/data/departments'
import { useSnackbar } from 'notistack'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Recruit } from 'src/models/Recruit'

const PageWrapper = styled(Box)(
  () => `
        display: flex;
        justify-content: center;
        align-items: center;
    `
)
const CustomTextareaAutosize = styled(TextareaAutosize)`
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

interface FormAPIResponse extends AxiosResponse {
  question1: string
  question2: string
  question3: string
}

interface ApplyAPIResponse extends AxiosResponse {
  answer1: string
  answer2: string
  answer3: string
}

function Apply() {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()
  const [questions, setQuestions] = useState(null)
  const params = useParams()
  const clubId = parseInt(params.id)
  const [values, setValues] = useState({
    answer1: null,
    answer2: null,
    answer3: null
  })

  const authHeader = useAuthHeader()
  const navigate = useNavigate()
  const authUser = useAuthUser()

  // 이미 회원인경우 접근 차단
  const isMember = authUser()?.clubs.filter(c => c.id === parseInt(params.id)).length > 0
  if (isMember) {
    enqueueSnackbar('이미 가입된 동아리입니다.', { variant: 'error' })
    navigate(-1)
  }

  // 학과 이름 파싱
  const college = Object.keys(departments).filter(college => departments[college].includes(authUser().department))[0]
  const departmentFullName =
    college === authUser()?.department ? authUser()?.department : `${college} ${authUser()?.department}`

  const handleChange = event => setValues({ ...values, [event.target.name]: event.target.value })
  const handleSubmit = e => {
    e.preventDefault()
    axios
      .post<ApplyAPIResponse>(
        `${process.env.REACT_APP_APIHOST}/apply/form?clubId=${params.id}&userId=${authUser().id}`,
        values,
        {
          headers: { Authorization: authHeader() }
        }
      )
      .then(() => {
        enqueueSnackbar('동아리에 지원서를 제출했어요! 좋은 결과 있기를 바라요 :)', { variant: 'success' })
        navigate(`/club/${clubId}`)
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/recruit?clubId=${params.id}`)
      .then((res: AxiosResponse<Recruit>) => {
        const startAt = new Date(res.data.startAt)
        const endAtNextDay = new Date(res.data.endAt)
        const today = new Date()

        // 마감일 23시 59분 59초까지 지원할 수 있도록 하루 더함
        endAtNextDay.setDate(endAtNextDay.getDate() + 1)

        // 지원 기간 내가 아닐 경우 접근 차단
        if (!(startAt <= today && today <= endAtNextDay)) {
          enqueueSnackbar('지금은 지원 기간이 아닙니다.', { variant: 'error' })
          navigate(-1)
        }
      })
      .catch(err => {
        enqueueSnackbar(`${err.response.data?.message}`, { variant: 'error' })
        navigate(-1)
      })

    axios
      .get<FormAPIResponse>(`${process.env.REACT_APP_APIHOST}/club/form?clubId=${clubId}`, {
        headers: { Authorization: authHeader() }
      })
      .then(res => {
        const { question1, question2, question3 } = res.data
        setQuestions([question1, question2, question3])
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [])

  return (
    <PageWrapper>
      <Helmet>
        <title>동아리 지원</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(8),
          padding: 3,
          flex: 1,
          maxWidth: '480px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          동아리 지원서
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <TextField
            id="email"
            type="email"
            label="이메일"
            defaultValue={authUser()?.email}
            inputProps={{ readOnly: true }}
          />
          <TextField
            id="name"
            type="text"
            label="이름"
            defaultValue={authUser()?.name}
            inputProps={{ disabled: true }}
          />
          <TextField
            id="student-id"
            type="text"
            label="학번"
            defaultValue={authUser()?.studentId}
            inputProps={{ readOnly: true }}
          />
          <TextField
            id="phone-number"
            type="tel"
            label="전화번호"
            defaultValue={authUser()?.phoneNumber}
            inputProps={{ readOnly: true }}
          />
          <FormControl sx={{ margin: 1, width: '100%' }} size="small" required>
            <InputLabel htmlFor="department">학과/학부</InputLabel>
            <Select
              id="department"
              label="학과/학부"
              defaultValue={departmentFullName}
              inputProps={{ readOnly: true }}
              fullWidth
            >
              {departments &&
                Object.keys(departments).map(college => [
                  <ListSubheader sx={{ fontWeight: 'bold' }}>{college}</ListSubheader>,
                  departments[college] &&
                    departments[college].map(depart => [
                      <MenuItem value={college === depart ? depart : college + ' ' + depart}>{depart}</MenuItem>
                    ])
                ])}
            </Select>
          </FormControl>
          {questions?.filter(q => q).length > 0 && (
            <>
              <Typography variant="h6" sx={{ marginTop: 1, marginBottom: theme.spacing(0.5) }}>
                지원서 질문
              </Typography>
              {questions.map(
                (q, i) =>
                  q && (
                    <Box width="100%" key={q} sx={{ marginBottom: 1 }}>
                      <Typography variant="subtitle1" sx={{ marginBottom: theme.spacing(0.5), textAlign: 'left' }}>
                        {q}
                      </Typography>
                      <CustomTextareaAutosize minRows={4} maxRows={6} name={`answer${i + 1}`} onChange={handleChange} />
                    </Box>
                  )
              )}
            </>
          )}
          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            지원서 제출
          </Button>
        </Box>
      </Paper>
    </PageWrapper>
  )
}

export default Apply
